.Background {
  background-color: white;
}

.OngoingProjects {
  padding-top: 50px;
  padding-bottom: 50px;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  display: flex;
  flex-direction: column;
  font-family: 'Athiti';
  letter-spacing: 2px;
}

.OngoingProjects h1 {
  margin: auto auto;
  font-size: 50px;
  padding-bottom: 30px;
}

@media (max-width: 999px) {
  .OngoingProjects {
    margin-left: 50px;
    margin-right: 50px;
  }
}

@media (max-width: 1345px) {
  .Wrap {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
  }  
}

@media (min-width: 1345px) {
  .Wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }  
}

.OngoingProjects .ProjectsHeading{
  display: flex;
  flex-direction: column;
  width: 95%;
  background-color: rgba(238, 231, 223, 1);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  padding-bottom: 20px;
}

.OngoingProjects .ProjectsHeading .Heading{
  font-size: 45px;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 700px) {
  .OngoingProjects .ProjectsHeading .Heading{
    font-size: 30px;
  }
}

@media (max-width: 500px) {
  .OngoingProjects .ProjectsHeading .Heading{
    font-size: 16px;
  }
}

.OngoingProjects .ProjectsHeading hr{
  width: 10%;
  border-top: 5px solid black;
}

.OngoingProjects .ProjectsHeading .SubHeading{
  font-size: 25px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;

}

@media (max-width: 700px) {
  .OngoingProjects .ProjectsHeading .SubHeading{
    font-size: 30px;
  }
}

@media (max-width: 500px) {
  .OngoingProjects .ProjectsHeading .SubHeading{

    font-size: 16px;
  }
}