.Box {
  width: 300px;
  margin: 50px 20px;
  opacity: 1;
  transition: opacity 150ms ease-out;
}

.Box:hover {
  opacity: 0.4;
}

.Box div {
  display: flex;
  flex-direction: column;
}

.Box div img {
  opacity: 1;
}

.Box div img {
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 180px;
  height: 180px;
}

