.Box div.Content {
  padding-top: 20px;
  padding-left: 35px;
  padding-right: 35px;
  display: flex;
  flex-direction: column;
}


.Box div.Content p{
  min-height: 50px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 25px;
  font-family: 'Athiti';
  letter-spacing: 2px;
  text-align: center;
}

@media (max-width: 500px) {
  .Box div.Content p{
    font-size: 20px;

  } 
}
