.Box {
  margin: 10px 20px;
  display: flex;
  flex-direction: column;
  background-color: white;
}

@media (max-width: 500px) {
  .Box {
    margin-bottom: 5px;
    margin: 10px 0px;
    width: 300px;
  }
}

@media (min-width: 500px) {
  .Box {
    margin-bottom: 5px;
    width:400px;
  }
}

@media (min-width: 768px) {
  .Box {
    width: 500px;
    margin-bottom: 5px;
  }
}

@media (min-width: 900px) {
  .Box {
    width: 500px;
    margin: 50px 20px;
  }
}

.Box div {
  display: flex;
  flex-direction: column;
}

.Box div .BottomModal {
  z-index: 100;
  height: 0px;
  background-color: black;
  transition-duration: 150ms;
  transition-property: height margin display;
  transition-timing-function: ease-in;
}

.Box div .BottomModal:hover {
  margin-top: -80px;
  height: 80px;
}

.Box div .BottomModal:hover .BottomModalText{
  display: flex;
}

.Box div img:hover + .BottomModal {
  margin-top: -80px;
  height: 80px;
}

.Box div img:hover + .BottomModal .BottomModalText{
  display: flex;
}

.Box div .BottomModal .BottomModalText{
  display: none;
  color: white;
  margin: auto;
  transition-duration: 350ms;
  transition-property: display;
  transition-timing-function: ease-in;
}

.Box div img {
  opacity: 1;
}

.Box div img:hover {
  cursor: pointer;
}

@media (min-width: 767px) {
  .Box div img {
    width: 500px;
    height: 300px;
  }
}

@media (max-width: 767px) {
  .Box div img {
    width: 300px;
    height: 180px;
  }
}

@media (max-width: 500px) {
  .Box div img {
    width: 200px;
    height: 120px;
  }
}

.Box div.Content {
  padding-top: 20px;
  padding-left: 35px;
  padding-right: 35px;
  display: flex;
  flex-direction: column;
  background-color: rgba(238, 231, 223, 1);
}


.Box div.Content p{
  min-height: 50px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 25px;
  font-family: 'Athiti';
  letter-spacing: 2px;
}

.Box div.Content .Price{
  display: flex;
  flex-direction: row;
  margin-left: auto;
  margin-right: auto;
}

.Box div.Content .Price .PrimeLogo{
  height: 75px;
  width: 75px;
}

@media(max-width: 500px) {
  .Box div.Content .Price .PrimeLogo{
    height: 60px;
    width: 60px;
  }
}

.Box div button.ViewProject {
  width: 100%;
  height: 60px;
  margin-bottom: 40px;
  background-color: rgba(0, 0, 0, 0);
  transition: background-color 0.2s ease;
  border: solid black 1px;
  font-size: 25px;
  letter-spacing: 2px;
  font-family: 'Athiti';
  color: black;
}

.Box div button.ViewProject:hover {
  border: solid black 3px;
  cursor: pointer;
}


@media (max-width: 500px) {
  .Box div.Content p{
    font-size: 20px;

  } 
}
